import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController } from '@ionic/angular';

import { RecipiesService } from '../recipies.service';
import { Recipie } from '../recipie.model';

@Component({
  selector: 'app-recipie-detail',
  templateUrl: './recipie-detail.page.html',
  styleUrls: ['./recipie-detail.page.scss']
})
export class RecipieDetailPage implements OnInit {
  loadedRecipe: Recipie;

  constructor(
    private activatedRoute: ActivatedRoute,
    private recipiesService: RecipiesService,
    private router: Router,
    private alertCtrl: AlertController
  ) { }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe(paramMap => {
      console.log(paramMap);
      if (!paramMap.has('recipeId')) {
        this.router.navigate(['/recipie']);
        console.log('not valid adress');
        return;
      }
      const recipeId = paramMap.get('recipeId');
      this.loadedRecipe = this.recipiesService.getRecipe(recipeId);
      if (Object.entries(this.loadedRecipe).length === 0) {
        this.router.navigate(['/recipie']);
      }
    });
  }

  onDeleteRecipe() {
    this.alertCtrl.create({
      header: 'Are you sure',
      message: 'Do you realy want to delete the recipie?',
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
      },
      {
        text: 'Delete',
        handler: () => {
          this.recipiesService.deleteRecipie(this.loadedRecipe.id);
          this.router.navigate(['/recipie']);
        }
      }]
    }).then(alertEl => {
      alertEl.present();
    });
  }

}

